<h1 id="python-introduction" tabindex="-1"><a class="header-anchor" href="#python-introduction" aria-hidden="true">#</a> Python Introduction</h1>
<p>In this lecture we take our first look at the programming language Python (without too many details) and go through how we can write Python code in files and then ask the <code>python</code> program to execute the Python code in a file for us. We also go through how we can write and run Python code using the Integrated Development Environment <a href="https://code.visualstudio.com/" target="_blank" rel="noopener noreferrer">Visual Studio Code<OutboundLink/></a>.</p>
